
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'



let echarts = require('echarts/lib/echarts')

require('echarts/lib/chart/bar')
require('echarts/lib/chart/line')

require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')

export default{
mixins: [common],
components:{
Group,
Cell,
Card,
Flexbox,
FlexboxItem
},
data () {
return {
popup_chart: false,
active_class: {},
charts: {},
yAxis: [],
series:[],
classes: [],
timeType: 'month'
}
},
watch: {
timeType: function (val) {
this.initChartData()
}
},
mounted() {
this.get_classes()
},
computed: {
enable_homework_star () {
let result = 0
if(this.$store.state.gvars.configs&&this.$store.state.gvars.configs.params){
result = this.$store.state.gvars.configs.params.service.enable_homework_star
}

return result
},
},
methods: {
changeTime (type) {
this.timeType = type
},
getTimeDay () {
let time_type = this.timeType
if(time_type=='week') {
let week_day = moment(new Date()).weekday()
return moment(new Date()).subtract(week_day-1,'days').format('YYYYMMDD')
}else if(time_type=='month'){
return moment(new Date()).format('YYYYMM')+'01'
}else{
return moment(new Date()).subtract(2,'months').format('YYYYMM')+'01'
}
},
getData (type) {
let [list,data] = [this.charts.complete_list,[]]
if(list && list.length > 0){
list.forEach((l,index)=>{
if(type=='times'){
data.push('第'+(index+1)+'次')
}else{
data.push(l[type])
}
})
}
return data
},
get_classes () {
let sid = this.$store.state.user.info.default_sid
if(sid>0){
this.$http.get('classes')
.then(response=>{
this.classes = response.body.data.list
if(this.classes.length===1){
this.choose_classes(this.classes[0])
}
}).catch(response=>{
console.log('response')
console.log(response)
this.toast(response.body.message,'text')
})
}
},
choose_classes (item) {
this.active_class = item
this.initChartData()
},
initChartData () {
let start_day = this.getTimeDay()
let uri = util.sprintf('homework_completes/student_status?cid=%s&start_day=%s&end_day=%s',this.active_class.cid,start_day,moment(new Date()).format('YYYYMMDD'))
this.$rest(uri).get()
.success(data=>{
this.charts = data
this.drawLine()
this.popup_chart = true
}).error(response=>{
this.toast(response.body.message||'获取失败','text')
})
},
drawLine() {
this.yAxis = []
if(this.enable_homework_star){
this.yAxis.push({
type : 'value', //value
min: 0,
max: 4,
interval: 1,
axisLine: {
lineStyle: {
width: '0'
}
},
axisLabel:{
textStyle: {
color: '#999999'
},
formatter: '{value} 星'
}
},{
type : 'value', //value
min: 0,
max: 10,
interval: 1,
axisLine: {
lineStyle: {
width: '0'
}
},
axisLabel:{
formatter: function (val) {
let ranges = {0:'',1:'D',2:'C-',3:'C',4:'C+',5:'B-',6:'B',7:'B+',8:'A-',9:'A',10:'A+'}
return ranges[val]
},
textStyle: {
color: '#999999'
}
}
})
this.series = [{
name:'星级',
type:'bar',
barWidth: '60%',
data: this.getData('star')
},
{
name:'评分',
type:'line',
yAxisIndex: 1,
smooth: true,
data: this.getData('result_level') ,
tooltip: {
trigger: 'item',
formatter: function (item) {
let ranges = {0:'0',1:'D',2:'C',3:'B',4:'A'}
let dot = '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color:#3398DB;"></span>'
return  item.name+'<br>'+ dot + item.seriesName + '：' + ranges[item.value]
}
}
}]
}else{
this.yAxis.push({
type : 'value', //value
min: 0,
max: 4,
interval: 1,
axisLine: {
lineStyle: {
width: '0'
}
},
axisLabel:{
formatter: function (val) {
let ranges = {0:'0',1:'D',2:'C',3:'B',4:'A'}
return ranges[val]
},
textStyle: {
color: '#999999'
}
}
})
this.series = [{
name:'评分',
type:'bar',
barWidth: '60%',
data: this.getData('result_level') ,
tooltip: {
trigger: 'item',
formatter: function (item) {
let ranges = {0:'0',1:'D',2:'C',3:'B',4:'A'}
let dot = '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color:#3398DB;"></span>'

return  item.name+'<br>'+ dot + item.seriesName + '：' + ranges[item.value]
}
}
}]
}


let myChart = echarts.init(document.getElementById('myChart'))

myChart.setOption({
color: ['#3398DB','orange'],
tooltip : {
trigger: 'axis',
axisPointer : {            // 坐标轴指示器，坐标轴触发有效
type : 'shadow'        
}
},
grid: {
left: '3%',
right: '4%',
bottom: '3%',
containLabel: true
},
xAxis : [
{
type : 'category',
data : this.getData('times'),
axisTick: {
alignWithLabel: true
},
axisLabel:{
textStyle: {
color: '#999999'
},
}
}
],
yAxis : this.yAxis,
series : this.series
});
}

}
}



